.container {
  width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}
@media screen and (max-width: 1199px) {
  .container {
    width: 970px;
  }
}
@media screen and (max-width: 991px) {
  .container {
    width: 750px;
  }
}
@media screen and (max-width: 767px) {
  .container {
    width: 100%;
    padding: 0 10px;
  }
}
body {
  background-color: #fff;
  color: #272c30;
  min-width: 320px;
  min-height: 100vh;
  font-family: "Roboto", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
* {
  box-sizing: border-box;
}
.chromeperfectpixel-overlay {
  width: 100% !important;
  height: 100% !important;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}
.main {
  display: flex;
}
@media screen and (max-width: 1700px) {
  .main {
    flex-direction: column;
  }
}
.menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px 0;
  min-height: 100vh;
  width: 13%;
  background-color: #263649;
}
.menu--hider {
  margin: 10px 0;
  padding: 0 15px;
  cursor: pointer;
}
.menu__block {
  padding: 12px 19px;
}
.menu__block:hover {
  background-color: #2e4157;
}
.menu__link {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 32px;
  font-weight: 400;
  color: #7e9eb9;
}
.menu__img {
  margin-right: 10px;
  width: 17px;
  height: 17px;
}
.line {
  width: 21px;
  height: 2px;
  background-color: #577594;
  margin: 5px 0;
}
.submenu {
  margin-top: -8px;
  margin-left: 70px;
}
.submenu__block {
  padding: 13px 0;
  color: #7e9eb9;
  list-style: initial;
}
.submenu__block:hover {
  background-color: #2e4157;
}
.submenu__link {
  padding: 0 5px;
  color: #7e9eb9;
}
.logout {
  display: flex;
  align-items: center;
  padding: 15px;
  cursor: pointer;
}
.logout__img {
  width: 19px;
}
.logout__text {
  padding-left: 10px;
  font-size: 14px;
  line-height: 32px;
  font-weight: 400;
  color: #98aec5;
}
@media screen and (max-width: 1700px) {
  .menu {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;
    min-height: 0;
  }
  .menu--top {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  .menu__blocks {
    display: flex;
    align-items: center;
  }
  .submenu {
    display: none;
  }
}
@media screen and (max-width: 479px) {
  .menu--top,
  .menu {
    justify-content: center;
  }
  .menu__blocks {
    flex-wrap: wrap;
    justify-content: center;
  }
}
.info {
  margin: 15px;
  width: 21%;
}
.info__wrapper {
  border: 1px solid #e6eaee;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
}
.info--top {
  padding: 25px;
}
.info__title {
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #272c30;
}
.info__status {
  display: flex;
  align-items: center;
  margin: 15px 10px;
}
.info__details {
  padding: 15px 25px;
  border-top: 1px solid #e6eaee;
  border-bottom: 1px solid #e6eaee;
}
.details__block {
  display: flex;
  margin: 6px;
}
.details__name {
  width: 150px;
  font-size: 14px;
  font-weight: 700;
  color: #272c30;
}
.details__status {
  width: 85px;
}
.back {
  margin-bottom: 15px;
  background-color: transparent;
  font-size: 40px;
  line-height: 32px;
  font-weight: 900;
  color: #7e9eb9;
  border: none;
  outline: none;
  cursor: pointer;
}
.confirmed,
.ghost {
  padding: 4px 10px;
  font-size: 11px;
  border-radius: 5px;
  font-weight: 400;
  letter-spacing: 0.1em;
  color: #ffffff;
}
.confirmed {
  background-color: #70c756;
}
.ghost {
  margin: 0 10px;
  opacity: 0.3;
  background-color: #263649;
}
.published,
.published__link {
  font-size: 14px;
  line-height: 32px;
  font-weight: 400;
  color: #91a5b8;
}
.published__link:hover {
  color: #263649;
}
.price {
  font-weight: 900;
  color: #272c30;
}
.sale {
  font-weight: 700;
}
.green {
  color: #70c756;
}
.red {
  color: #ed4e67;
}
.blue {
  color: #6699ea;
}
.up,
.down {
  font-weight: 900;
}
.date {
  padding: 0 5px;
  background-color: #ed4e6759;
  border-radius: 5px;
}
.inspection {
  font-weight: 700;
}
.info--bottom {
  margin: 15px 25px;
}
.input__block {
  position: relative;
}
.aircraft__notes {
  font-size: 14px;
  line-height: 28px;
  font-weight: 700;
  color: #272c30;
}
.aircraft__input {
  margin-bottom: 10px;
  padding: 10px 40px 10px 10px;
  width: 100%;
  min-height: 58px;
  background-color: #ffffff;
  border: 1px solid #eceff1;
  vertical-align: middle;
  outline: none;
  -webkit-appearance: none;
}
.aircraft__input::placeholder {
  padding: 10px;
  font-size: 15px;
  line-height: 21px;
  font-weight: 400;
  color: #90a4ae;
}
.aircraft__send {
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  margin-left: -35px;
  background: url("../img/send_msg.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  border: none;
  cursor: pointer;
}
.msg {
  overflow-y: scroll;
  height: 360px;
}
.msg::-webkit-scrollbar {
  width: 0.5em;
  cursor: pointer;
}
.msg::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
.msg::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}
.msg__block {
  margin: 0 10px 5px 0;
  padding: 5px 10px;
  background-color: #f1f6ff;
}
.msg__info {
  font-size: 12px;
  font-weight: 700;
  color: #90a4ae;
}
.msg__text {
  font-size: 14px;
  line-height: 28px;
  font-weight: 700;
  color: #272c30;
}
@media screen and (max-width: 1700px) {
  .info {
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;
  }
}
.status {
  margin: 25px 25px 25px 0;
  width: 60%;
}
.status__menu {
  display: flex;
  margin-top: 40px;
  border-bottom: 4px solid #e6eaee;
}
.status__menu__item {
  margin-right: 40px;
  margin-bottom: 11px;
  font-size: 16px;
  line-height: 32px;
  font-weight: 700;
  color: #272c30;
  cursor: pointer;
}
.status__menu__item:hover {
  margin-bottom: -4px;
  color: #6699ea;
  border-bottom: 4px solid #6699ea;
}
.status__search {
  display: flex;
  align-items: flex-end;
  margin-top: 20px;
  margin-bottom: 30px;
}
.status__info {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-auto-rows: 40px;
  border: 1px solid #eceff1;
}
.status__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 21px;
  font-size: 14px;
  line-height: 32px;
  font-weight: 900;
  color: #272c30;
  border: 1px solid #eceff1;
}
.status__date {
  display: flex;
  align-items: center;
  padding: 0 21px;
  border: 1px solid #eceff1;
}
.status__date:nth-child(even) {
  background-color: #fdfeff;
}
.status__update {
  display: flex;
  align-items: center;
  padding: 0 21px;
  border: 1px solid #eceff1;
}
.status__update:nth-child(odd) {
  background-color: #fdfeff;
}
.status__value {
  display: flex;
  align-items: center;
  padding: 0 21px;
  border: 1px solid #eceff1;
}
.status__value:nth-child(even) {
  background-color: #fdfeff;
}
.status__previous {
  border-bottom: 1px solid #eceff1;
}
.status__previous:nth-child(odd) {
  background-color: #fdfeff;
}
.status__new {
  border-bottom: 1px solid #eceff1;
}
.status:nth-child(even) {
  background-color: #fdfeff;
}
.border {
  border-right: none;
  border-left: none;
}
.search__block:nth-child(1) {
  position: relative;
}
.search__title {
  margin-bottom: 9px;
  font-size: 13px;
  line-height: 1.2;
  font-weight: 400;
  color: #272c30;
}
.search__input {
  margin-right: 25px;
  padding-right: 30px;
  width: 253px;
  height: 36px;
  border: 1px solid #eceff1;
}
.search__input::placeholder {
  padding: 11px;
}
.search__submit {
  position: absolute;
  top: 30px;
  right: 35px;
  border: none;
  background-color: transparent;
  outline: none;
}
.search__btn {
  padding: 10px 60px;
  color: #fff;
  background-color: #6699ea;
  border: none;
  border-radius: 5px;
  outline: none;
  cursor: pointer;
}
.search__btn:hover {
  box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.14);
}
@media screen and (max-width: 1700px) {
  .status {
    width: 100%;
    padding: 0 20px;
    margin: 0 auto;
    margin-bottom: 100px;
  }
  .status__info {
    overflow-x: scroll;
  }
  .status__info::-webkit-scrollbar {
    width: 0.5em;
    cursor: pointer;
  }
  .status__info::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }
  .status__info::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    outline: 1px solid slategrey;
  }
  .status__title {
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
    line-height: 1;
  }
  .status__search,
  .status__menu {
    flex-wrap: wrap;
  }
  .search__btn {
    margin-top: 20px;
  }
  .search__title {
    margin-top: 10px;
  }
  .search__submit {
    top: 40px;
  }
}
